<template>
	<div class="pos">
		<el-row>
			<el-col :span='7'  class="pos-order" id="order-list">
			<el-tabs>
				<el-tab-pane label="点餐">
					<el-table :data="tableData" border width="100%" stripe>
						<!-- stripe为隔行变色 -->
						<el-table-column prop="goodsName" label="商品名称"></el-table-column>
						<el-table-column prop="count" label="数量" width="50"></el-table-column>
						<el-table-column prop="price" label="金额" width="70"></el-table-column>
						<el-table-column  label="操作" width="100" fixed="right">
							<template scope="scope">
								<el-button type="text" size="small" @click="deleGoods(scope.row)">删除</el-button>
								<el-button type="text" size="small" @click="addFoods(scope.row)">添加</el-button>
							</template>	
						</el-table-column>
					</el-table>
					<div class="totalDiv">
						<small>数量：</small>{{totalCount}}&nbsp;&nbsp;&nbsp;&nbsp;
						<small>金额：</small>{{totalMoney}}
					</div>
					<div class="div-btn">
						<el-button type="warning">接单</el-button>
						<el-button type="danger" @click="deleAllGoods">删除</el-button>
						<el-button type="success" @click="checkout">结账</el-button>
					</div>
				</el-tab-pane>
				<el-tab-pane label="挂单">挂单</el-tab-pane>
				<el-tab-pane label="外卖">外卖</el-tab-pane>
			</el-tabs>
			</el-col>
			<el-col :span="17">
				<div class="often-goods">
					<div class="goods-title">常用商品</div>
					<div class="goods-list">
						<ul>
							<li v-for="goods in oftenGoods" @click="addFoods(goods)">
								<span>{{goods.goodsName}}</span>
								<span class="goods-price">￥{{goods.price}}</span>
							</li>
						</ul>
					</div>
				</div>
				<div class="goods-type">
					<el-tabs>
						<el-tab-pane label="汉堡">
							<ul class="cookList">
								<li v-for="types in type0Goods" @click="addFoods(types)">
									<span class="foodsImg"><img :src="types.goodsImg" width="36" height="36"></span>
									<span class="foodsName">{{types.goodsName}}</span>
									<span class="foodsPrice">￥{{types.price}}</span>
								</li>
							</ul>
						</el-tab-pane>
						<el-tab-pane label="小吃">
							<ul class="cookList">
								<li v-for="types in type1Goods" @click="addFoods(types)">
									<span class="foodsImg"><img :src="types.goodsImg" width="36" height="36"></span>
									<span class="foodsName">{{types.goodsName}}</span>
									<span class="foodsPrice">￥{{types.price}}</span>
								</li>
							</ul>
						</el-tab-pane>
						<el-tab-pane label="饮料">
							<ul class="cookList">
								<li v-for="types in type2Goods" @click="addFoods(types)">
									<span class="foodsImg"><img :src="types.goodsImg" width="36" height="36"></span>
									<span class="foodsName">{{types.goodsName}}</span>
									<span class="foodsPrice">￥{{types.price}}</span>
								</li>
							</ul>
						</el-tab-pane>
						<el-tab-pane label="套餐">
							<ul class="cookList">
								<li v-for="types in type3Goods" @click="addFoods(types)">
									<span class="foodsImg"><img :src="types.goodsImg" width="36" height="36"></span>
									<span class="foodsName">{{types.goodsName}}</span>
									<span class="foodsPrice">￥{{types.price}}</span>
								</li>
							</ul>
						</el-tab-pane>
					</el-tabs>
				</div>
			</el-col>
		</el-row>
	</div>
</template>
<script>
	import axios from 'axios'
	export default{
		name:'Pos',
		data(){
		return{
		tableData:[],
        oftenGoods:[],
      	type0Goods:[],
      	type1Goods:[],
      	type2Goods:[],
      	type3Goods:[],
      	totalMoney:0,
      	totalCount:0
		}
		},
		created:function(){
			axios.get('http://jspang.com/DemoApi/oftenGoods.php')//成功
			.then(response=>{
				console.log(response);
				this.oftenGoods=response.data
			})//请求失败
			.catch(error=>{
				console.log(error);
				alert('网络错误，不能访问')
			})//失败

			axios.get('http://jspang.com/DemoApi/typeGoods.php')//成功
			.then(response=>{
				console.log(response);
				this.type0Goods=response.data[0];
				this.type1Goods=response.data[1];
				this.type2Goods=response.data[2];
				this.type3Goods=response.data[3];
			})//请求失败
			.catch(error=>{
				console.log(error);
				alert('网络错误，不能访问')
			})//失败
		},
		mounted:function(){//当所有的虚拟dom加载完成时候执行
		var orderHeight=document.body.clientHeight;//订单高度
		console.log(orderHeight)
		document.getElementById('order-list').style.height=orderHeight+'px';

		},
		methods:{
			addFoods(goods){
				this.totalMoney=0;
				this.totalCount=0;
				console.log(goods)//该goods是个对象
				//商品是否已经存在订单中
				let isHave=false;
				for( let i=0;i<this.tableData.length;i++){
					if(this.tableData[i].goodsId==goods.goodsId){
						isHave=true;
					}
				}
				//根据判断的值编写业务逻辑
				if(isHave){
					//改变列表中商品的数量
					let arr=this.tableData.filter(a=>a.goodsId==goods.goodsId);
					arr[0].count++;
				}else{
					//不存在就加入该新商品到数组中
					let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1}
					this.tableData.push(newGoods)
				}
				this.getAllMoney()

			},
		// 汇总数量金额
			getAllMoney(){
				this.totalCount=0;
				this.totalMoney=0;
				if(this.tableData){
					//计算汇总金额和数量
				this.tableData.forEach((element)=>{
					this.totalCount+=element.count;
					this.totalMoney+=(element.price*element.count);
				})
				}
			},
			checkout(){
				if(this.totalCount!=0){
					this.tableData=[];
					this.totalCount=0;
					this.$message({
						message:'结账成功，共计'+this.totalMoney+'元',
						type:'success'
					})
					this.totalMoney=0;
				}else{
					this.$message.error('小伙子，你很闲啊')
				}
			},

			deleGoods(goods){
				console.log(goods);
				this.tableData=this.tableData.filter(a=>a.goodsId!=goods.goodsId);
				this.getAllMoney()
			},
			deleAllGoods(){
				this.tableData=[];
				this.totalCount=0;
				this.totalMoney=0;

			}
		}
	}
</script>
<style scoped>
	.pos-order{
	background-color:#f9fafc;
	border-right:1px solid skyblue;
	}
	.div-btn{
		margin-top:10px;
	}
	.goods-title{
		height:20px;
		border-bottom:1px solid #d3dce6;
		background:#f9fafc;
		padding:10px;
		text-align:left;
	}
	.goods-list ul li{
		cursor:pointer;
		list-style:none;
		float:left;
		border:1px solid #e5e9f2;
		padding:10px;
		margin:16px;
		background:#fff;
	}
	.goods-price{
		color:#58b7ff;
	}
	.goods-type{
		clear:both;
		padding:10px;
	}
	.cookList li{
		position:relative;
		list-style:none;
		border:1px solid #e5e9f2;
		background:#fff;
		width:23%;
		overflow:hidden;
		float:left;
		padding:10px;
		margin:10px;
		height:auto;
		cursor:pointer;
	}
	.cookList li span{
		display:block;
		float:left;
	}
	.foodsName{
		padding-left:10px;
		font-size:15px;
		color:red;
	}
	.foodsPrice{
		position:absolute;
		top:30px;
		left:50px;
		padding-top:5px;
		padding-left:10px;
	}
</style>


